.form-elements {
    margin: 90px;
    padding: (140px - 90px) 0 0 (140px - 90px);
    // display: grid;
    // grid-template-columns: 33.33% 33.33% 33.33%;
    // column-gap: 20px;
    // position: relative;
    @import './Form_Element_1/Form_Element_1.scss';
    // @import './Form_Element_2/Form_Element_2.scss';
    // @import './Form_Element_3/form-element_3.scss';
    @media (max-width: 1240px) {
        min-width: 768px;
        grid-template-columns: 50% 50%;
        row-gap: 20px;
    }
    @media (max-width: 767px) {
        min-width: $min_width;
        grid-template-columns: 100%;
        row-gap: 20px;
    }
}